import React,{Component, useState} from 'react'
import MenuItems from './menuItems'
import {FaHamburger,FaBook, FaWindowClose} from 'react-icons/fa'
import{Button} from '../button';
import './navbar.css'
function Navbar() {
  console.log(MenuItems);
  const [hamburgerClicked, setHamburgerClicked] = useState(false);
  function handleHamburgerClick(e){    
    setHamburgerClicked(!hamburgerClicked);
  }
    return (
        <nav className="navbar">
          <h1 className="navbar-logo">NavTitle<FaBook/></h1>
          <ul className ={hamburgerClicked===false?'nav-menu':'nav-menu active'}>
            {MenuItems.map((item, index)=>{
              return <li key={index}>
                        <a href={item.url} className={item.cName}>
                          {item.title}
                        </a>
                    </li>
            })}
          </ul>
          <Button>SignUp</Button>
          <div className="menu-icon" >
            {hamburgerClicked===false?<FaHamburger onClick={handleHamburgerClick}/>:<FaWindowClose onClick={handleHamburgerClick}/>}            
          </div>
        </nav>
    )
}

export default Navbar
